.botUl {
  display: flex;
  flex-wrap: wrap;
  width: 97%;
  margin: auto;
  justify-content: space-around;
}


.botLi {
  width: 21%;
  list-style: none;
  border: thin solid grey;
  margin: 7px;
  border-radius: 12px;
  box-shadow: 10px 10px 8px #0091ff;
  line-height: 1.5em;
  word-break: break-all;
  background-color: rgb(255, 166, 0);
  color: rgb(255, 255, 255);

}

span {
  padding-right: 5px;
}

.deleteBtn {
  margin: 2px;
  background-color: rgb(255, 0, 0);
  color: rgb(255, 255, 255);
  border-radius: 50px;
  padding: 5px;
  width: 18%;

}

header {
  width: 100%;
  background-color: rgb(255, 128, 0);
  padding: 10px;
  font-size: 3vw;
  font-weight: bolder;
  color: white;
  margin-bottom: 20px;
}

.armyDiv {
  min-height: 350px;
  width: 97%;
  background-color: rgb(255, 162, 0);
  margin: 10px auto 40px auto;
  border-radius: 10px;
}

.LinkCollection {
  color: rgb(255, 255, 255);
  text-decoration: none;
}

.specDiv {
  width: 70%;
  background-color: #E1D9D1;
  border: thin solid gray;
  margin: 10px auto 40px auto;
  height: 500px;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  align-items: center;
}

.specContent {
  text-align: start;
  line-height: 2em;
}

.btnSpec>button {
  margin: 10px 22px 10px 10px;
  padding: 6px;
  border-radius: 5px;
  border: 1px solid black;
  background-color: black;
  color: white;
  width: 120px;
}

.navSpec {
  width: 200px;
  color: white;
  text-decoration: none;
  font-weight: bold;
  padding: 20px;


}
